	module.exports = {"data":{"markdownRemark":{"html":"<h3>Automatic Invitations for Private Programs</h3>\n<p>We now enable private programs to configure a minimum threshold for their report volume under which new hackers will be automatically invited. </p>\n<h3>Hacktivity Redesign</h3>\n<p>We’ve redesigned Hacktivity so that we surface educational reports from interesting hackers. </p>\n<h3>Hacker Header on Reports</h3>\n<p>All reports now include a header with summarized stats on the hacker who submitted the report. The new header fields include:</p>\n<ul>\n<li>The hacker name</li>\n<li>Reputation</li>\n<li>Rank</li>\n<li>Signal</li>\n<li>Signal Percentile</li>\n<li>Impact</li>\n<li>Impact Percentile</li>\n</ul>","frontmatter":{"path":"/changelog/2016/april","title":"April 2016"}}},"pathContext":{}}

